
        @import "@/assets/scss/All.scss";
        















































































































































































































































































.body {
  background: #eee;
}
.top {
  position: fixed;
  margin: 0 auto;
  left: 0;
  right: 0;
  margin: auto;
  z-index: 10;
  border: 1px solid #eeeeee;
  background: #fff;
}
.rank,
.fuel {
  font-size: 0.7em;
  .sub {
    color: #cf5724;
    display: block;
  }
  > div {
    width: 100%;
    margin: auto;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: wrap;
    input {
      margin: 0 5px;
      float: left;
      -webkit-appearance: none;
      position: relative;
      cursor: pointer;
      outline: none !important;
      border: 1px solid #eeeeee;
      border-radius: 2px;
      background: #fbfbfb;
    }
    input[type='checkbox']::before {
      content: '\2713';
      position: absolute;
      top: 50%;
      left: 50%;
      overflow: hidden;
      transform: scale(0) translate(-50%, -50%);
      line-height: 1;
    }
    input[type='checkbox']:checked {
      background-color: #ff8955;
      border-color: #ffffff4d;
      color: white;
    }
    input[type='checkbox']:checked::before {
      border-radius: 2px;
      transform: scale(1) translate(-50%, -50%);
    }
  }
  .date_time {
    font-size: 0.7em;
    display: flex;
    flex-direction: column;
    button {
      width: 70px;
    }
  }
  .sort {
    li {
      margin: 0 5px;
      a {
        color: #5e5e5e;
      }
      a:link {
        color: #5e5e5e;
      }
      a:visited {
        color: #5e5e5e;
      }
      a:active {
        color: #5e5e5e;
      }
    }
  }
}
.search_box {
  #search {
    background: #e4e4e4;
    border: 1px solid #5e5e5e;
    border-left: 0;
    width: 50px;
    height: 30px;
    line-height: 30px;
  }
  #reset {
    line-height: 30px;
  }
  .contents_list {
    width: 701px;
    margin: auto;
    padding-top: 224px;
    border: 1px solid #eeeeee;
    background-color: #ffffff;
    border-top: 0;
  }
}
@media all and(max-width:767px) {
  .top {
    width: 100%;
    padding: 20px;
    padding-top: 60px;
    height: 260px;
  }
  .filter {
    display: flex;
    flex-flow: row;
    flex-wrap: wrap;
    > div {
      width: 50%;
    }
  }
  .rank,
  .fuel {
    .sub {
      font-size: 1.6em;
      text-align: center;
      line-height: 0.8em;
    }
    > div {
      input {
        width: 12px;
        height: 12px;
      }
    }
    .line_h {
      margin: 10px 0 5px;
      line-height: 10px;
      font-size: 1em;
    }
  }
  .date_time {
    flex-wrap: wrap;
    width: 100%;
    margin: auto;
    .sub {
      color: #cf5724;
      font-size: 1.2em;
      text-align: center;
    }
  }
  .sort {
    flex-direction: column;
    font-size: 0.8em;
    ul {
      display: flex;
      justify-content: center;
      line-height: 30px;
    }
    .search_box {
      display: flex;
      flex-direction: row;
      justify-content: center;
      flex-wrap: wrap;
      .stage-search {
        font-size: 0.8em;
        width: 50%;
      }
    }
  }
  .contents_list {
    width: 100%;
    margin: auto;
    padding-top: 260px;
    border: 1px solid #eeeeee;
    background-color: #ffffff;
    border-top: 0;
  }
  .contents {
    background: #fff;
    border: 1px solid #e4e4e4;
    display: flex;
    margin: auto;
    flex-wrap: wrap;
    flex-flow: column;
    .img_box {
      width: 100%;
      // height: 200px;
      // border: 1px solid red;
      order: 0;
      overflow: hidden;
      background: #fff;
      a {
        img {
          height: 150px;
        }
      }
    }
    .contents_box {
      display: flex;
      flex-wrap: wrap;
      // border: 1px solid red;
      height: 140px;
      width: 100%;
      justify-content: space-between;
      .c_box_01 {
        order: 1;
        text-align: left;
        .rank_name {
          font-weight: bold;
          font-size: 1.4em;
        }
        dd {
          font-size: 1em;
        }
      }
      .c_box_02 {
        display: flex;
        flex-flow: column;
        justify-content: space-between;
        order: 2;
        text-align: right;
        .car_name {
          padding: 10px 20px;
          display: block;
          color: #fff;
          font-size: 1.2em;
          background: #ff8955;
        }
        .star {
          font-size: 1em;
          margin: 10px 20px;
          color: #ff8955;
        }
      }
      .c_box_03 {
        position: relative;
        order: 3;
        width: 100%;
        text-align: right;
        align-self: flex-end;
        font-size: 2em;
        color: #cf5724;
        button {
          font-size: 0.6em;
          line-height: 2em;
          padding: 0 20px;
          border: 0;
          color: #fff;
          background: #cf5724;
        }
        button:hover {
          background: #ff8955;
        }
      }
    }
  }
}
@media all and(min-width:768px) {
  .top {
    width: 701px;
    padding: 20px;
    padding-top: 80px;
  }
  .filter {
    display: flex;
    > div {
      width: 47%;
      margin-left: 3%;
    }
  }
  .rank,
  .fuel {
    .sub {
      margin-left: 5px;
      font-size: 1.8em;
      text-align: left;
    }
    > div {
      input {
        width: 16px;
        height: 16px;
      }
    }
    .line_h {
      margin: 10px 0 5px;
      line-height: 14px;
      font-size: 1.2em;
    }
  }
  .date_time {
    .sub {
      font-size: 1.2em;
      text-align: left;
      color: #cf5724;
    }
  }
  .sort {
    display: flex;
    justify-content: space-between;
    height: 30px;
    margin-left: 3%;
    flex-direction: row;
    a {
      color: #5e5e5e;
    }
    ul {
      display: flex;
      justify-content: flex-start;
      line-height: 40px;
      width: 30%;
      li {
        a:hover {
          color: #cf5724;
        }
      }
    }
    .search_box {
      width: 45%;
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
      .stage-search {
        font-size: 0.8em;
        width: 70%;
        text-align: left;
      }
      #search:hover {
        background: #ff8955;
        color: #fff;
        border-left: 0;
        border: 1px solid #cf5724;
      }
      #reset:hover {
        color: #cf5724;
      }
    }
  }
  .contents_list {
    width: 702px;
    margin: auto;
    padding-top: 224px;
    border: 1px solid #eeeeee;
    background-color: #ffffff;
    border-top: 0;
  }
  .contents {
    background: #fff;
    border: 1px solid #e4e4e4;
    display: flex;
    width: 701px;
    height: 200px;
    margin: auto;
    .img_box {
      width: 250px;
      height: 200px;
      // border: 1px solid red;
      margin-right: 20px;
      order: 0;
      overflow: hidden;
      background: #fff;

      a {
        img {
          width: 100%;
        }
      }
      > a:hover img {
        transform: scale(1.5);
        transition: transform 1s;
        filter: brightness(70%);
        background-color: #5e5e5e;
      }
    }
    .contents_box {
      display: flex;
      flex-wrap: wrap;
      // border: 1px solid red;
      height: 200px;
      width: 450px;
      justify-content: space-between;
      .c_box_01 {
        order: 1;
        text-align: left;
        .rank_name {
          font-weight: bold;
          font-size: 2em;
        }
        dd {
          font-size: 1.2em;
        }
      }
      .c_box_02 {
        display: flex;
        flex-flow: column;
        justify-content: space-between;
        order: 2;
        text-align: right;
        .car_name {
          padding: 10px 20px;
          display: block;
          color: #fff;
          font-size: 1.6em;
          background: #ff8955;
        }
        .star {
          font-size: 1.4em;
          margin: 10px 20px;
          color: #ff8955;
        }
      }
      .c_box_03 {
        position: relative;
        top: -1px;
        order: 3;
        width: 100%;
        text-align: right;
        align-self: flex-end;
        font-size: 3em;
        color: #cf5724;
        button {
          font-size: 0.6em;
          line-height: 2em;
          padding: 0 20px;
          border: 0;
          color: #fff;
          background: #cf5724;
        }
        button:hover {
          background: #ff8955;
        }
      }
    }
  }
}
